}

.p-success__dlRow {
  display: flex;
  padding-top: 1.125rem;
  padding-bottom: 1.125rem;
  border-top: 1px solid #e9e9e9;
}

.p-success__dlRow:last-child {
  border-bottom: 1px solid #e9e9e9;
}

.p-success__dt {
  font-size: 0.875rem;
  letter-spacing: 0.05em;
  line-height: 2.1428571429;
  font-weight: 400;
  color: #242424;
}
@media screen and (min-width: 768px) {
  .p-success__dt {
    width: 10.625rem;
  }
}

.p-success__dd {
  font-size: 1rem;
  line-height: 1.875;
  letter-spacing: 0.05em;
  font-weight: 400;
  color: #242424;
}
@media screen and (min-width: 768px) {
  .p-success__dd {
    width: calc(100% - 10.625rem);
  }
}

.p-top-news {
  background: #ebebeb;
  padding-bottom: 8.3125rem;
}

.p-top-news__content {
  padding-top: 6.25rem;
  padding-bottom: 6.25rem;
  filter: drop-shadow(0 0 20px rgba(0, 0, 0, 0.05));
  background: #fff;
}
@media screen and (min-width: 768px) {
  .p-top-news__content {
    display: flex;
    border-top-right-radius: 0.3125rem;
    border-bottom-left-radius: 0.3125rem;
    border-top-left-radius: 2.5rem;
    border-bottom-right-radius: 2.5rem;
    padding-top: 5rem;
    padding-bottom: 5rem;
    padding-left: 6.25rem;
    padding-right: 6.25rem;
    gap: 6.25rem;
  }
}
@media screen and (min-width: 1540px) {
  .p-top-news__content {
    border-top-left-radius: 3.75rem;
    border-bottom-right-radius: 3.75rem;
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
    padding-left: 9.375rem;
    padding-right: 9.375rem;
    gap: 10rem;
  }
}

@media screen and (min-width: 1540px) {
  .p-top-news__left {
    max-width: 15.0625rem;
    width: 100%;
  }
}

.p-top-news__btn-wrapper {
  margin-top: 2.5625rem;
}

.p-top-new__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3.125rem;
  border: 1px solid #00a040;
  border-radius: 9999px;
  width: 15.0625rem;
  height: 3.125rem;
}
.p-top-new__btn-text {
  font-size: 1rem;
  color: #00a040;
  font-weight: 400;
}

.p-top-new__btn-arrow {
  display: inline-block;
  width: 0.9375rem;
}

.p-top-new__btn-arrow svg {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}

.p-top-new__btn-arrow img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}

.p-top-new__right {
  width: 100%;
  flex: 1;
}

.p-top-news__list-link {
  display: block;
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  border-top: 1px solid #e9e9e9;
}

.p-top-news__list:last-child .p-top-news__list-link {
  border-bottom: 1px solid #e9e9e9;
}

.p-top-news__meta {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}

.p-top-news__category {
  font-size: 0.75rem;
  color: #00a040;
  background: rgba(0, 160, 64, 0.2);
  border-radius: 9999px;
  padding: 0.4375rem 1.5625rem;
}

.p-top-news__time {
  font-size: 0.875rem;
  color: #242424;
  font-family: "lato", sans-serif;
  font-weight: 400;
}

.p-top-news__list-title {
  letter-spacing: 0.05em;
  line-height: 1.8888888889;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .p-top-news__list-title {
    margin-top: 0.625rem;
    font-size: 1rem;
  }
}
@media screen and (min-width: 1540px) {
  .p-top-news__list-title {
    font-size: 1.125rem;
  }
}

.p-top-works {
  padding-top: 12.5rem;
