.p-top-works__slider-wrapper {
  margin-top: 4.5rem;
  margin-right: calc(50% - 50vw);
}

.p-top-works__slider + .p-top-works__slider {
  margin-top: 7.5rem;
}

.c-intro-title__en {
  display: block;
  position: relative;
  font-family: "lato", sans-serif;
  font-weight: 500;
  font-size: 0.75rem;
  letter-spacing: 0.05em;
  color: #242424;
  padding-left: 1.5625rem;
}

.c-intro-title__en::before {
  position: absolute;
  content: "";
  background: url(../images/common/intro_title_icon.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 0.9375rem;
  height: auto;
  aspect-ratio: 15/10;
  object-fit: cover;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.c-intro-title__en--white {
  color: #fff;
  position: relative;
}

.c-intro-title__en--white::before {
  position: absolute;
  content: "";
  background: url(../images/common/intro_title_icon_white.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 0.9375rem;
  height: auto;
  aspect-ratio: 15/10;
  object-fit: cover;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.c-intro-title__ja {
  display: block;
  color: #00a040;
  font-size: 3.125rem;
  font-weight: 700;
  letter-spacing: 0.05em;
}

.c-intro-title__ja--white {
  color: #fff;
}

.c-main-title__en {
  font-weight: 900;
  font-size: 1.25rem;
  color: #00a040;
  display: block;
  font-family: "lato", sans-serif;
}

.c-main-title__ja {
  color: #00a040;
  font-weight: 700;
  display: block;
}
@media screen and (min-width: 768px) {
  .c-main-title__ja {
    font-size: 3.75rem;
  }
}
@media screen and (min-width: 1540px) {
  .c-main-title__ja {
    font-size: 5.625rem;
  }
}

@media screen and (min-width: 768px) {
  .c-main-title__ja--small {
    font-size: 2.5rem;
  }
}
@media screen and (min-width: 1540px) {
  .c-main-title__ja--small {
    font-size: 3.75rem;
  }
}

.c-main-title__ja--white {
  color: #fff;
}

.c-main-title__en--white {
  color: #fff;
}

.c-small-title {
  color: #00a040;
  font-size: 2.125rem;
  letter-spacing: 0.05em;
  font-weight: 400;
  text-align: center;
  display: inline-block;
  position: relative;
}

.c-small-title--white {
  color: #fff;
}

.c-small-title::before {
  position: absolute;
  content: "";
  background: #00A040;
  width: 1.25rem;
  height: 3px;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%);
}

.c-small-title--white::before {
  background: #fff;
}